import React from "react";
import { NavLink } from 'react-router-dom';
import './css/support.css'



const Verification = (props) => {



    return (
        <div className='app-main'>
            <main className="page-main support">
                <div className="container support-container">
                    <p className="support-title">
                        <div className="navigate-back support-title-back">
                            <NavLink to="/support" className="App-link">
                                <svg className="navigate-back-svg" width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                    <path d="M4 8L12 16L20 8" stroke="#000000" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"></path>
                                </svg>
                            </NavLink>
                        </div>Verification</p>
                    <div className="support-content"><div className="support-html-block"><div className="support-html-block-content"><div className="tile support-html-block support-html-block-tile false"><div className="support-html-block-item"><p className="support-html-block-item-title">If the application for verification was rejected, can I reapply for verification?</p><div className="support-html-block-item-content"><p><span  >There can be several reasons for rejecting an application:</span></p>
                        <p><span  >- The photo is of poor quality;</span></p>
                        <p><span  >- Mismatch between the Name or Surname indicated in the documents and those indicated at registration;</span></p>
                        <p><span  >- Invalid documents.</span></p>
                        <p>&nbsp;</p>
                        <p><span  >If an application is rejected, we always give you the reason.</span></p>
                        <p><span  >If your profile was rejected, you can always go back to the necessary step, correct the mistake or upload a better quality photo of the document, and then send the application for reconsideration (except if your application is rejected because your profile does not meet KYC standards).</span></p>
                        <p>&nbsp;</p>
                        <p><span  >To resume the verification process, log in to your account and go to <a href="https://vpwallet.io/verification/">https://vpwallet.io/verification</a></span></p></div></div>
                        <div className="support-line "></div>
                        <div className="support-html-block-item"><p className="support-html-block-item-title">How do I get verified in VP Wallet?</p><div className="support-html-block-item-content"><p>
                            <span  >1) The first step - you need to check and accept the privacy terms and agree to the processing of your personal data.</span></p>
                            <p className="wysiwyg-text-align-left"></p>
                            <p><span  >The next step will only appear if verification is initiated from a PC. You need to scan the QR-code using the application on your smartphone and, using the received link, continue the verification on your PC or smartphone.</span></p>
                            <p className="wysiwyg-text-align-left"></p>
                            <p><span  >2) The second step- you need to specify general information: name and surname, e-mail, phone number and go to the confirmation of identity.</span></p>
                            <p className="wysiwyg-text-align-left"></p>
                            <p><span  >3) The third step- require to take your picture. To do this, simply follow the instructions, which will tell you the right angle and distance to the camera. Also make sure that your face is well lit and the image is not cloudy.</span></p>
                            <p className="wysiwyg-text-align-left"><span  ></span></p>
                            <p><span  >4) Fourth step - according to Know Your Customer international standards, you can confirm your identity with an official government document - a passport or an ID card.</span></p>
                            <p><span  >Choose your type of ID and upload photos of your document. Verification takes up to two minutes.</span></p>
                            <p className="wysiwyg-text-align-left"></p>
                            <p><span  >5) The fifth step - the system will ask you to provide a document confirming your residential address. This can be a utility bill, internet bill, or bank statement.</span></p>
                            <p className="wysiwyg-text-align-left"><span  ></span></p>
                            <p className="wysiwyg-text-align-left"><span  >What is important to keep in mind when you take a photo:</span></p>
                            <p><span  >&nbsp;</span></p>
                            <p><span  >*All corners of the document should be visible in the photo.</span></p>
                            <p><span  >*The text in the document should be easy to read. Make sure the photo is free of glare and blemishes. Poor quality images will not be accepted by the system.</span></p>
                            <p><span  >*If your document has two pages or two sides (such as a French ID card or CIS passport), both sides/pages must be attached.</span></p>
                            <p><span  >*There must be at least one month left before the document expires. An expired document will not be accepted by the system.</span></p>
                            <p><span  >*The system will not accept documents that have been edited in any graphic editors.</span></p>
                            <p><span  >*Please note that citizens of the Russian Federation must present a foreign passport as an identification document (Russian passport, unfortunately, does not comply with international standards).</span></p>
                            <p><span  >*We accept JPEG or PDF files of at least 500 KB in size.</span></p>
                            <p><span  >*In some cases the document should have Latin transliteration, as the system does not accept Arabic ligatures.</span></p>
                        </div></div></div></div></div></div>
                    <div className="contact-us support-container-contact">Have any question? Please <span className="contact-us-clickable">contact us</span></div>
                </div>
            </main>
        </div>
    )
}

export default Verification;