import React from "react";
import phone_pc from './img/iphonePc.svg';
import phone_circle from './img/circlePc.svg';
import phone_mob_pc from './img/phoneMobile.svg';
import phone_mob_circle from './img/circleMobile.svg';
import './css/wallet-promo.css';
import { NavLink } from 'react-router-dom';


const Wallet_promo = () =>{
    return(
    
            <div className="wallet-promo">
              <div className="wallet-promo-main">
                <p className="wallet-promo-main-title">
                  The wallet that works for you
                </p>
                <p className="wallet-promo-main-description">
                  Open your e-wallet for fast and comfortable worldwide payments
                </p>
                <NavLink to="/signup" className="App-link">
                <button className="button wallet-promo-main-button filled-green-button ">
                  <span>
                    Get a free account
                  </span>
                </button>
                </NavLink>
                <div className="promo-phone">
                  <img className="promo-phone-pc" src={phone_pc} alt="iphone" />
                  <img className="promo-phone-circle" src={phone_circle} alt="circle" />
                  <img className="promo-phone-mobile" src={phone_mob_pc} alt="iphone-mobile" />
                  <img className="promo-phone-mobile-circle" src={phone_mob_circle} alt="circle-mobile" />
                </div>
              </div>
            </div>

    )
}

export default Wallet_promo;