import React from "react";
import { NavLink} from 'react-router-dom';
import './css/supports.css'



const Support = (props) => {

   

    return (
        <div className='app-main'>
             
          <main className="page-main support">
  <div className="container support-container">
    <p className="support-title">How can we help?</p>
    <div className="support-content">
      <div>
      <NavLink to="/suppotr/account" className="App-link-support">
        <div className="support-menu-link">Account</div>
        </NavLink>
        <NavLink to="/suppotr/verification" className="App-link-support">
        <div className="support-menu-link">Verification</div>
        </NavLink>
        <NavLink to="/suppotr/wallet-top-up" className="App-link-support">
        <div className="support-menu-link">Wallet top up</div>
        </NavLink>
        <NavLink to="/suppotr/wallet-transfer" className="App-link-support">
        <div className="support-menu-link">Wallet transfer</div>
        </NavLink>
        <NavLink to="/suppotr/country-issues" className="App-link-support">
        <div className="support-menu-link">Country issues</div>
        </NavLink>
      </div>
    </div>
    <div className="contact-us support-container-contact">Have any question? Please 
    <span className="contact-us-clickable"> contact us</span></div>
  </div>
</main>
        </div>
    )
}

export default Support;
