            <p className="legal-main-text">Under data protection laws, such as Regulation (EU) 2016/679 of the European Parliament and of the Council of 27 April 2016 (GDPR), Personal Data Protection, Law on Legal Protection of the Czech Republic and of Personal Data and other applicable laws, we are required to provide you with certain information about who we are, how we process your Personal Data and for what purposes, and your rights in relation to your Personal Data.</p>
