import React from "react";
import './css/product.css';
import { NavLink } from 'react-router-dom';



const Product = (props) => {
    return (


        <div className={props.product_class}>
            <div className="product-card-text">
                <p className="product-card-text-title">
                    {props.product_title}</p>
                <p className="product-card-text-info">
                    {props.product_text}</p>
            </div>
            <div className="product-card-footer">
                <div className="green-redirect ">
                <NavLink to="/signin" className="App-link">
                    <p className="green-redirect-text">
                        Start store</p>
                        </NavLink>
                    <svg className="" width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M19 12L4 12" stroke="#367A54" stroke-width="2" stroke-linecap="round"></path>
                        <path d="M12 4L20 12L12 20" stroke="#367A54" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"></path>
                    </svg>
                </div>
                <img className="product-card-footer-image products-wallet-image" src={props.product_img} alt={props.product_title} />
                <img className="product-card-footer-mobile products-wallet-image" src={props.product_img_mob} alt={props.product_title} />
            </div>
        </div>




    )
}

export default Product;
