import React from "react";
import { NavLink} from 'react-router-dom';
import './css/supports.css'



const Legal = (props) => {

   

    return (
        <div className='app-main'>
             
          <main className="page-main support">
  <div className="container support-container">
    <p className="support-title">Legal</p>
    <div className="support-content">
      <div>
      <NavLink to="/legal/terms-and-conditions" className="App-link-support">
        <div className="support-menu-link">Terms and conditions</div>
        </NavLink>
        <NavLink to="/legal/privacy-policy" className="App-link-support">
        <div className="support-menu-link">Privacy Policy</div>
        </NavLink>
        <NavLink to="/legal/prohibited-activity-list" className="App-link-support">
        <div className="support-menu-link">Prohibited activity list</div>
        </NavLink>
       
      </div>
    </div>
    <div className="contact-us support-container-contact">Have any question? Please 
    <span className="contact-us-clickable"> contact us</span></div>
  </div>
</main>
        </div>
    )
}

export default Legal;
