import React from "react";
import { NavLink } from 'react-router-dom';
import './css/support.css'



const WalletTopUp = (props) => {



    return (
        <div className='app-main'>
            <main className="page-main support">
                <div className="container support-container">
                    <p className="support-title">
                        <div className="navigate-back support-title-back">
                            <NavLink to="/support" className="App-link">
                                <svg className="navigate-back-svg" width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                    <path d="M4 8L12 16L20 8" stroke="#000000" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"></path>
                                </svg>
                            </NavLink>
                        </div>Wallet top up</p>
                    <div   className="support-content">
                        <div   className="support-html-block">
                            <div   className="support-html-block-content">
                                <div   className="tile support-html-block support-html-block-tile false">
                                    <div   className="support-html-block-item">
                                        <p   className="support-html-block-item-title">How do I top up my wallet with a bank card?</p>
                                        <div   className="support-html-block-item-content">
                                            <ol>
                                                <li  aria-level="1"><span  >Choose the necessary wallet and open the Top Up page in the "Wallets" section</span></li>
                                                <li  aria-level="1"><span  >Enter the amount you want to charge and click "Top up" buttom.</span></li>
                                                <li  aria-level="1"><span  >In the opened page, enter the data of the bank card from which you want to deposit wallet. The commission will be calculated automatically. Click "Pay".</span></li>
                                                <li  aria-level="1"><span  >After a successful charge, the system will return you to the "Wallets" page. In a few minutes, your wallet balance will be replenished by the amount of the order.</span></li>
                                            </ol>
                                            <p><span  >Wallet can be replenished only by bank card issued on your name and having a valid period of validity.</span></p>
                                        </div>
                                    </div>
                                    <div className="support-line "></div>
                                    <div   className="support-html-block-item">
                                        <p   className="support-html-block-item-title">Why is recharge by card declined?</p>
                                        <div   className="support-html-block-item-content">
                                            <p><span  >There are several reasons why a card recharge may be declined:</span></p>
                                            <p><span  >&nbsp;- You entered the card details incorrectly.</span></p>
                                            <p><span  >&nbsp;- We have not received a reply from the issuing bank. This may be caused by technical problems at the bank or slow internet connection.</span></p>
                                            <p><span  >&nbsp;- You have not verified your identity. If you have encountered difficulties in verification, please read the information on </span><span  >Verification</span><span  >.</span></p>
                                            <p>&nbsp;</p>
                                            <p><span  >If you entered all the data correctly, and your internet speed is high, but the transaction still does not go through, make sure the card meets our requirements:</span></p>
                                            <p><span  >&nbsp;- The 3D Secure technology must be enabled.</span></p>
                                            <p><span  >&nbsp;- Internet payments with your card must be allowed.</span></p>
                                            <p><span  >&nbsp;- The balance must be sufficient.</span></p>
                                            <p><span  >&nbsp;- Only cards issued in your name are accepted for funds transfers.</span></p>
                                            <p><span  >&nbsp;- The transaction has exceeded any card limits on your bank's side.</span></p>
                                            <p><span  >&nbsp;- Make sure there are no transaction limits on your bank's side of the MCC code.</span></p>
                                            <p>&nbsp;</p>
                                            <p><span  >To verify that your card meets our requirements, contact the bank issuer.</span></p>
                                            <p><span  >If after checking all the conditions mentioned above you are still declined a deposit from your card, please create a </span><span  >ticket</span><span  > with a detailed description of the situation.</span></p>
                                        </div>
                                    </div>
                                    <div   className="support-html-block-item">
                                        <p   className="support-html-block-item-title">After cancelling the transaction the funds were not returned to the card. What should I do?</p>
                                        <div   className="support-html-block-item-content">
                                            <p><span  >We cannot affect the time of cancellation of pre-authorization. If enough time has passed and the funds have not been returned, please contact your card issuing bank directly.</span></p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="contact-us support-container-contact">Have any question? Please <span className="contact-us-clickable">contact us</span></div>
                </div>
            </main>
        </div>
    )
}

export default WalletTopUp;