import React from "react";
import { NavLink } from 'react-router-dom';
import './css/support.css'



const WalletTransfer = (props) => {



    return (
        <div className='app-main'>
            <main className="page-main support">
                <div className="container support-container">
                    <p className="support-title">
                        <div className="navigate-back support-title-back">
                            <NavLink to="/support" className="App-link">
                                <svg className="navigate-back-svg" width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                    <path d="M4 8L12 16L20 8" stroke="#000000" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"></path>
                                </svg>
                            </NavLink>
                        </div>Wallet transfer</p>
                    <div  className="support-content">
                        <div  className="support-html-block">
                            <div  className="support-html-block-content">
                                <div  className="tile support-html-block support-html-block-tile false">
                                    <div  className="support-html-block-item">
                                        <p  className="support-html-block-item-title">How do I transfer funds to my bank card?</p>
                                        <div  className="support-html-block-item-content">
                                            <ol>
                                                <li   aria-level="1"><span  >In the required wallet&nbsp; click on "Transfer" button, then "Withdraw".</span></li>
                                                <li   aria-level="1"><span  >In the appears window, enter the number of the card, the name of the cardholder and the amount of transfer. The commission will be calculated automatically. Carefully check all data and press "Confirm".&nbsp;</span></li>
                                                <li   aria-level="1"><span  >After a successful charge, the system will return you to the "Wallets" page.</span></li>
                                            </ol>
                                        </div>
                                    </div>
                                    <div className="support-line "></div>
                                    <div  className="support-html-block-item">
                                        <p  className="support-html-block-item-title">When will the transfer be credited to the card?</p>
                                        <div  className="support-html-block-item-content">
                                            <p><span  >The timing of crediting funds depends on how quickly the recipient's bank processes the transaction. Transactions usually take up to 3 business days.</span></p>
                                        </div>
                                    </div>
                                    <div className="support-line "></div>
                                    <div  className="support-html-block-item">
                                        <p  className="support-html-block-item-title">The transfer to the card did not go through</p>
                                        <div  className="support-html-block-item-content">
                                            <p><span  >If the transaction doesn't go through, check to see if:</span></p>
                                            <ol>
                                                <li   aria-level="1"><span  >The currency of the wallet corresponds to the currency of the card&nbsp;</span></li>
                                                <li   aria-level="1"><span  >Limits of the recipient card allow for new transfers.</span></li>
                                                <li   aria-level="1"><span  >The recipient's card number was entered correctly.</span></li>
                                                <li   aria-level="1"><span  >There are enough funds on your account for the transfer.</span></li>
                                            </ol>
                                            <p><span  >If everything is fine, please contact our support team via a </span><span  >ticket</span><span  > describing your inquiry in detail.</span></p>
                                        </div>
                                    </div>
                                    <div className="support-line "></div>
                                    <div  className="support-html-block-item">
                                        <p  className="support-html-block-item-title">The transaction was successful, but the funds were not credited to the card</p>
                                        <div  className="support-html-block-item-content">
                                            <p><span  >If the transaction appears to be successful but the funds have not been received after 3 business days, please contact our support team via a </span><span  >ticket</span><span  >.</span></p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="contact-us support-container-contact">Have any question? Please <span className="contact-us-clickable">contact us</span></div>
                </div>
            </main>
        </div>
    )
}

export default WalletTransfer;