import React from "react";
import { NavLink } from 'react-router-dom';
import './css/support.css'



const Verification = (props) => {

   

    return (
        <div className='app-main'>
      <main class="page-main support">
        <div className="container support-container">
  <p className="support-title">
    <div className="navigate-back support-title-back">
    <NavLink to="/support" className="App-link">
      <svg className="navigate-back-svg" width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
        <path d="M4 8L12 16L20 8" stroke="#000000" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"></path>
      </svg>
      </NavLink>
    </div>Account</p>
  <div className="support-content">
    <div className="tile support-account">
      <p className="support-subtitle">What is VP Wallet?</p>
      <p>VP Wallet is your secure e-wallet for fast and convenient payments worldwide. Our mission is to create an open financial system used worldwide and become a leading global brand that helps our customers convert whatever currency is convenient.</p>
      <div className="support-line "></div>
      <p className="support-subtitle">What are the requirements for my password?</p>
      <p>To meet VP Wallet platform security criteria your password should consist of at least 8 letters, numbers and special characters, which will include at least one letter, one number and one special character.</p>
      <div className="support-line "></div>
      <p className="support-subtitle">I forgot my password. How can I reset it?</p>
      <p>To reset your password, follow this link</p>
      <div className="support-line "></div>
      <p className="support-subtitle">I haven't received my account activation email. What should I do?</p>
      <p>Please make sure you entered your email address correctly and check your spam folder. If you haven't received your activation code email within 30 minutes, please contact our support team here.</p>
      <div className="support-line "></div>
      <p className="support-subtitle">How can I make my account more secure?</p>
      <p><strong>VP Wallet staff will never ask for your password, two-factor authentication codes, or other login credentials. We will never ask you to install remote login or remote support software on your computer.</strong></p>
      <br/>
      <p>If anyone claiming to be associated with VP Wallet support requests this information, contact us immediately.</p>
      <br/>
      <p><strong>Password</strong></p>
      <p>Use a complex and unique password that is not generic or similar to those used on any other websites. These guidelines provide some information about what makes a password secure. It is also recommended that you change your password periodically (about every 3 months) and use a completely new password each time. Using a password manager such as 1Password or LastPass makes it easier to manage. </p>
      <br/>
      <p>Another important note: Never give your password to anyone. A VP Wallet employee will NEVER ask for your password</p>
      <br/>
      <p><strong>Two-Factor Authentication</strong></p>
      <p>Take advantage of the two-factor authentication (2FA) services we offer, which can either be an additional SMS login code or a TOTP application such as Google Authenticator. You can enable it on your security page by adding an Authenticator or phone number. You can also include two-factor authentication codes when you send it as an additional security measure.</p>
      <br/>
      <p>TOTP applications are much more secure than SMS for 2FA codes because they are tied to a specific physical device rather than a phone number, which can be compromised in a phone transfer attack. </p>
      <br/>
      <p>If you use 2FA SMS codes, call your service provider and add additional protections to your account. For example, ask for a PIN or password for your account, ask for a port freeze, and lock your SIM card (so attackers can't move your phone number to a new carrier). If your carrier doesn't support these additional protections, consider switching to that carrier.</p>
      <br/>
      <p><strong>Email address</strong></p>
      <p>Your email address is one of the most important connections between you and your VP Wallet account ! Make sure it's secure! https://haveibeenpwned.com/ can be used to determine if your email address has ever been compromised by a third party as a result of a data breach. If so, create a new email address to use with your VP Wallet account.</p>
      <br/>
      <p>Do not use the same password in your email account as in your VP Wallet account. If your email provider suggests it, activate two-factor authentication (2FA) in your email account to add an extra layer of security.</p>
      <br/>
      <p><i>* VP Wallet is not affiliated with haveibeenpwned.com and does not represent its products or services. Use as you see fit.</i></p>
    </div>
  </div>
  <div className="contact-us support-container-contact || ''">Have any question? Please <span className="contact-us-clickable">contact us</span></div>
</div>
</main>
        </div>
    )
}

export default Verification;