import React from 'react';
import {Route} from 'react-router-dom';
import './css/signin.css';
import {useState} from 'react';



const Signin = () => {


    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState(null);
    const [ type, setType ] = useState('password');
    const [activeState, setActiveState] = useState(false);
    
    const compet =  activeState;
    const passsetActiveState = () => {
       
     setActiveState(prev => !prev);
        if (!activeState) { setType('text') ; } else {setType('password') ;}
    }
    // const passType = () => {
    //     setActiveState(prev => !prev);
    //     if (flag === true) {setType('text') ;  flag = false;} else
    //     if (flag === false) {setType('password');  flag = true; }
    // }
    // var flag = true;
    
    
    function isValidEmail(email) {
      return /\S+@\S+\.\S+/.test(email);
    }
  
    const handleChange = event => {
      if (!isValidEmail(event.target.value)) {
        setError('Invalid email address');
      } else {
        setError(null);
      }
  
      setEmail(event.target.value);
    };
    const passChange = event => {
       
        setPassword(event.target.value);
      };

  return (
    
    <div className="app-main ">
    <main className="page-main login">
      <div className="container form-container">
        <div className="form-container-content">
          <div className="form-container-content-label login-content">
            <p>Sign In</p>
          </div>
          <form className="login-form">
            <div className="login-form-content">
              <div className="input login-form-content-input">
                <input className="input-field" type="text" name="email" placeholder="E-mail"
                value={email}
                onChange={handleChange}
                />
                
                {<div className="input-error">{error}</div>}
                <div className="input-error"></div>
                <div className="input-detail"></div>
                <div className="input-reveal"></div>
              </div>
              <div className="input login-form-content-input login-form-content-password">
                <input className="input-field" name="password" placeholder="Password"
                 value={password}
                 type={type}
                onChange={passChange}
                />
                <div className="input-error"></div>
                <div className="input-error"></div>
                <div className="input-detail"></div>
                
                    <div  onClick={passsetActiveState} className={`input-reveal ${activeState ? 'active' : ''}`}>
                  <svg className="" width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <circle cx="12" cy="12" r="4" stroke="#7F7F84" stroke-width="2"></circle>
                    <path d="M2.3753 11.2191C1.94404 11.5641 1.87412 12.1934 2.21913 12.6247C2.56414 13.056 3.19343 13.1259 3.6247 12.7809L2.3753 11.2191ZM20.3753 12.7809C20.8066 13.1259 21.4359 13.056 21.7809 12.6247C22.1259 12.1934 22.056 11.5641 21.6247 11.2191L20.3753 12.7809ZM3 12C3.6247 12.7809 3.62457 12.781 3.62445 12.7811C3.62444 12.7811 3.62435 12.7811 3.62433 12.7812C3.62428 12.7812 3.62431 12.7812 3.62441 12.7811C3.62462 12.7809 3.62512 12.7805 3.62592 12.7799C3.62753 12.7786 3.63032 12.7764 3.63427 12.7733C3.64218 12.767 3.65474 12.7572 3.67178 12.7439C3.70585 12.7173 3.75778 12.6772 3.8261 12.6255C3.9628 12.5221 4.16479 12.3724 4.42043 12.192C4.9327 11.8304 5.65537 11.3487 6.49614 10.8682C8.21967 9.88337 10.2763 9 12 9V7C9.72374 7 7.28033 8.11663 5.50386 9.13176C4.59463 9.65132 3.8173 10.1696 3.26707 10.558C2.99146 10.7526 2.77157 10.9154 2.61921 11.0307C2.543 11.0884 2.4836 11.1342 2.44248 11.1663C2.42192 11.1823 2.40591 11.1949 2.39466 11.2038C2.38903 11.2082 2.38459 11.2117 2.38136 11.2143C2.37974 11.2156 2.37843 11.2166 2.37742 11.2174C2.37691 11.2178 2.37648 11.2182 2.37613 11.2185C2.37595 11.2186 2.37575 11.2188 2.37566 11.2188C2.37547 11.219 2.3753 11.2191 3 12ZM12 9C13.7237 9 15.7803 9.88337 17.5039 10.8682C18.3446 11.3487 19.0673 11.8304 19.5796 12.192C19.8352 12.3724 20.0372 12.5221 20.1739 12.6255C20.2422 12.6772 20.2941 12.7173 20.3282 12.7439C20.3453 12.7572 20.3578 12.767 20.3657 12.7733C20.3697 12.7764 20.3725 12.7786 20.3741 12.7799C20.3749 12.7805 20.3754 12.7809 20.3756 12.7811C20.3757 12.7812 20.3757 12.7812 20.3757 12.7812C20.3757 12.7811 20.3756 12.7811 20.3755 12.7811C20.3754 12.781 20.3753 12.7809 21 12C21.6247 11.2191 21.6245 11.219 21.6243 11.2188C21.6243 11.2188 21.624 11.2186 21.6239 11.2185C21.6235 11.2182 21.6231 11.2178 21.6226 11.2174C21.6216 11.2166 21.6203 11.2156 21.6186 11.2143C21.6154 11.2117 21.611 11.2082 21.6053 11.2038C21.5941 11.1949 21.5781 11.1823 21.5575 11.1663C21.5164 11.1342 21.457 11.0884 21.3808 11.0307C21.2284 10.9154 21.0085 10.7526 20.7329 10.558C20.1827 10.1696 19.4054 9.65132 18.4961 9.13176C16.7197 8.11663 14.2763 7 12 7V9Z" fill="#7F7F84"></path>
                    <path d="M3 20L21 4" className='strokecolor' stroke="#7F7F84" stroke-width="2" stroke-linecap="round"></path>
                  </svg>
                </div> 
               
              </div>
            </div>
            <button className="button filled-green-button login-form-button button-disabled" disabled={(!email || !password)}>Sign In</button><a className="route-link login-form-forgot" href="/password-forgot" target=""><span>Forgot password?</span></a>
            <div></div>
          </form>
        </div>
      </div>
    </main>
    
  </div>
  );
}

export default Signin;
