import React from 'react';
import logo from './img/vpwallet.png';
import './css/header.css';
import { NavLink } from 'react-router-dom';
import menuImg from './img/menu.svg';
import closemenuImg from './img/close.svg';
import { useState } from 'react';
const Header = () => {

  const [checked_menu, setChecked_menu] = useState(false);
  const handleChange_menu = () => {
    
    setChecked_menu(!checked_menu);
  };
  return (
    <header className="App-header">
      <div className='App-nav container menu-content'>
        <div className='menu-content-actions'>
          <div className='menu_logo'>
            <NavLink to="/">
              <img src={logo} className="logo" alt="logo" href='/' />
            </NavLink>
          </div>
          <div className='menu-actions'>
            <div className='public-actions'>
              <div class="public-actions-mobile">
                <div class="mobile-button public-actions-mobile">
                <input className="checkbox-box-menu" 
                                type="checkbox"
                                checked={checked_menu}
                                onChange={handleChange_menu}
                                wfd-id="id11" />
                  <div class="mobile-button-img">
                    <img src={menuImg} alt="menu" className={`${!checked_menu ? 'active_open' : ''}`}/>
                    <img src={closemenuImg} alt="menu" className={`${checked_menu ? 'active_close' : ''}`} />
                    </div>
                </div>
              </div>
               <div  className={`public-actions-mobile-content ${checked_menu ? 'active_menu' : ''}`} >
  <div className="public-actions-routes ">
    <a className="route-link public-menu-link" href="/signin" target="">
      <button className="button outline-green-button public-actions-routes-login ">Sign in</button>
    </a>
    <a className="route-link public-menu-link" href="/signup" target="">
      <button className="button filled-green-button public-actions-routes-register ">Sign up</button>
    </a>
  </div>
</div> 
              <div class="public-actions-content">
                <div className='public-actions-routes '>
                  <NavLink to="/signin" className="App-link">
                    <button className="button outline-green-button public-actions-routes-login route-link public-menu-link">
                      Sign in
                    </button>
                  </NavLink>
                

                <div>
                  <NavLink to="/signup" className="App-link">
                    <button className="button filled-green-button public-actions-routes-register route-link public-menu-link">
                      Sign up
                    </button>
                  </NavLink>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </header>
  );
}

export default Header;

